import React from 'react';
import Slider from 'react-slick';
import { titleClass } from '../tailwindStyles';
import { settingsSlide } from '../utils/constants';
import TitleCard from './TitleCard';

const FavoritesContainer = ({ books }) => {
	return (
		<div className="max-h-50 ">
			<h1 className={titleClass}>Favorites</h1>
			<Slider className="max-h-60 md:hidden " {...settingsSlide}>
				{books.map((book) => {
					return <TitleCard key={book.id} book={book} />;
				})}
			</Slider>
			<div className="  hidden md:flex flex-wrap ">
				{books.map((book) => {
					return <TitleCard key={book.id} book={book} favorite={true} />;
				})}
			</div>
		</div>
	);
};

export default FavoritesContainer;
